import styled from 'styled-components';
import { Card } from 'reactstrap';

export const CardContainer = styled(Card)`
  margin: 5px;

  &:first-child {
    margin-left: 0px;
  }

  &:last-child {
    margin-right: 0px;
  }

  .card-body {
    padding: 10px;
    font-weight: 500;
    font-size: 12px;

    .watch-btn {
      font-weight: 500;
      font-size: 12px;
    }
  }
`;
